import {
  ActionIcon,
  Button,
  Group,
  NumberInput,
  Select,
  Space,
  Stack,
  Text,
  Title,
} from "@mantine/core";
import "./App.css";
import { useState } from "react";
import { IconMinus, IconPlus } from "@tabler/icons-react";

const W1 = [1, 1, 2];
const W2 = [1, 1, 1, 1];

function App() {
  const [rowWeights1, setRowWeights1] = useState(W1);
  const [rowWeights2, setRowWeights2] = useState(W2);

  const handleUpdateRowWeights1 = (rowWeights: Array<number>): void => {
    setRowWeights1(rowWeights);
  };
  const handleUpdateRowWeights2 = (rowWeights: Array<number>): void => {
    setRowWeights2(rowWeights);
  };

  return (
    <Stack p={32}>
      <Title order={3}>Settings</Title>
      <Settings
        rowWeights1={rowWeights1}
        rowWeights2={rowWeights2}
        onUpdateRowWeights1={handleUpdateRowWeights1}
        onUpdateRowWeights2={handleUpdateRowWeights2}
      />

      <Space />
      <Title order={3}>Layout</Title>
      <Layout rowWeights1={rowWeights1} rowWeights2={rowWeights2} />
    </Stack>
  );
}

/////

type WeightsListProps = {
  label: string;
  initialWeights: Array<number>;
  onUpdate: (values: Array<number>) => void;
};
function WeightsList(props: WeightsListProps) {
  const [weights, setWeights] = useState(props.initialWeights);

  function handleIncrement(index: number) {
    const nextCounters = weights.map((c, i) => {
      if (i === index) {
        return Math.min(5, c + 1);
      } else {
        return c;
      }
    });
    setWeights(nextCounters);
    props.onUpdate(nextCounters);
  }
  function handleDecrement(index: number) {
    const nextCounters = weights.map((c, i) => {
      if (i === index) {
        return Math.max(1, c - 1);
      } else {
        return c;
      }
    });
    setWeights(nextCounters);
    props.onUpdate(nextCounters);
  }

  return (
    <Stack spacing={4}>
      <Text size={"sm"}>{props.label}</Text>
      <Group spacing={32}>
        {weights.map((counter, index) => (
          <Group key={index} spacing="xs" className="weights-list">
            <ActionIcon
              disabled={counter <= 1}
              variant="filled"
              onClick={() => {
                handleDecrement(index);
              }}
            >
              <IconMinus size="1rem" />
            </ActionIcon>
            <span className="weight-label">{counter}</span>
            <ActionIcon
              disabled={counter >= 5}
              variant="filled"
              onClick={() => {
                handleIncrement(index);
              }}
            >
              <IconPlus size="1rem" />
            </ActionIcon>
          </Group>
        ))}
      </Group>
    </Stack>
  );
}

//

type SettingsProps = {
  rowWeights1: Array<number>;
  rowWeights2: Array<number>;
  onUpdateRowWeights1: (rowWeights: Array<number>) => void;
  onUpdateRowWeights2: (rowWeights: Array<number>) => void;
};
function Settings(props: SettingsProps) {
  return (
    <Stack>
      <WeightsList
        label="Row 1 weights"
        initialWeights={props.rowWeights1}
        onUpdate={props.onUpdateRowWeights1}
      />
      <WeightsList
        label="Row 2 weights"
        initialWeights={props.rowWeights2}
        onUpdate={props.onUpdateRowWeights2}
      />
    </Stack>
  );
}

/////

type LayoutProps = {
  rowWeights1: Array<number>;
  rowWeights2: Array<number>;
};
function Layout(props: LayoutProps) {
  const sum = (numbers: Array<number>): number => {
    return numbers.reduce((accumulator, currentValue) => {
      return accumulator + currentValue;
    }, 0);
  };

  const rowWeights1 = props.rowWeights1;
  const rowWeights2 = props.rowWeights2;

  const rowTotal1 = sum(rowWeights1);
  const rowTotal2 = sum(rowWeights2);

  return (
    <div className="container">
      <div
        className="row-grid "
        style={{ gridTemplateColumns: "repeat(" + rowTotal1 + ", 1fr)" }}
      >
        <Select
          className={"weight-" + rowWeights1[0]}
          label="Flow"
          data={[
            { value: "Algo", label: "Algo" },
            { value: "DMA", label: "DMA" },
            { value: "Worked", label: "Worked" },
          ]}
          value="Algo"
          withAsterisk
        />
        <Select
          className={"weight-" + rowWeights1[1]}
          label="Type"
          data={[
            { value: "Limit", label: "Limit" },
            { value: "Market", label: "Market" },
            { value: "Stop", label: "Stop" },
          ]}
          value="Limit"
          withAsterisk
        />
        <NumberInput
          className={"weight-" + rowWeights1[2]}
          defaultValue={100}
          label="Quantity"
          withAsterisk
        />
      </div>
      <div
        className="row-grid"
        style={{ gridTemplateColumns: "repeat(" + rowTotal2 + ", 1fr)" }}
      >
        <NumberInput
          className={"weight-" + rowWeights2[0]}
          defaultValue={145}
          label="Limit Price"
        />
        <NumberInput
          className={"weight-" + rowWeights2[1]}
          disabled
          label="Stop Price"
        />
        <NumberInput
          className={"weight-" + rowWeights2[2]}
          disabled
          label="Display Quantity"
        />
        <NumberInput
          className={"weight-" + rowWeights2[3]}
          defaultValue={14500}
          label="Notional"
        />
      </div>
    </div>
  );
}

/////

export default App;
